@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.uberAlles{ 
  background-color: rgb(233, 233, 233);
  border:black solid 1px;
}
.App {
  margin: auto;
  font-family: Rale;
}
.container{
  width: 80%;
  margin: auto;
  margin-top:70px;
}
.header {
  display: flex;
  justify-content: space-between;
  font-family: 'Open Sans', sans-serif;
}

.headerInfo{
  margin: 0px;
}
.profilePhoto {
  height:200px;
  width:auto;
}

.openSans{
  font-family: 'Open Sans', sans-serif;
  margin: auto;
  margin-bottom: 15px;
}
.jobWrapper{
  margin-bottom: 20px;
}

.jobWrapper:hover{
  background-color: rgb(225, 221, 221);
  transition: 1s;
}
.jobInfo {
  display: flex;
  justify-content: space-between;
  font-family: 'Open Sans', sans-serif;
}
.jobName {
  font-style: italic;
  margin: auto;
  font-family: 'Open Sans', sans-serif;
}
ul {
  margin:auto;
  margin-top: 10px;
  text-align: left;
  font-family: 'Open Sans', sans-serif;
  text-align: justify;

}

li{
  list-style-type: square ; 
}


.educationWrapper {
  margin-bottom: 20px;

}

.educationInfo {
  display: flex;
  justify-content: space-between;
}

.educationWrapper:hover{
  background-color: rgb(225, 221, 221);
  transition: 1s;
}
.schoolInfo {
  margin: 0px;
  font-family: 'Open Sans', sans-serif;
}
.schoolDegree{
  font-style: italic;
  font-family: 'Open Sans', sans-serif;
}
.schoolDescription{
  font-family: 'Open Sans', sans-serif;
}
